.profile__description {
    font-family: Inter, Arial, Helvetica, sans-serif;
    font-style: normal;
    font-weight: normal;
    font-size: 18px;
    line-height: 22px;
    margin: 0;
    padding: 0;
    max-width: 300px;
}

@media screen and (max-width: 635px) {
    .profile__description {
        font-size: 14px;
        line-height: 17px;
        text-align: center;
    }
}